import React, { useEffect, useState } from "react";
import axios from "axios";
import "./India.css";
import { indiaUrl } from "../../utils/url";
import { Row } from "react-bootstrap";
import { CircularProgressbarWithChildren } from "react-circular-progressbar";
function India() {
  const [status, setStatus] = useState([]);
  useEffect(() => {
    fetchStatus();
  }, []);

  const fetchStatus = async () => {
    const response = await axios.get(indiaUrl);
    const st = response.data;
    console.log(st[st.length - 1]);
    setStatus(st[st.length - 1]);
  };
  function numDifferentiation(value) {
    var val = Math.abs(value);
    if (val >= 10000000) {
      val = (val / 10000000).toFixed(2) + " Cr";
    } else if (val >= 100000) {
      val = (val / 100000).toFixed(2) + " Lac";
    }
    return val;
  }

  const circleStyleI = {
    width: "220px",
    height: "220px",
    fontSize: "10px",
    textAlign: "center",
  };
  return (
    <Row
      className="row"
      style={{
        textTransform: "capitalize",
        margin: "2rem",
        padding: "1rem",
        textAlign: "center",
        alignItems: "center",
        justifyContent: "center",
      }}
    >
      <h1 style={{ margin: "1rem" }}>{status.Country}</h1>
      {/* <h2  style={{ margin: "1rem" }}>{status.Date}</h2> */}

      <div className="circlestaus" style={circleStyleI}>
        <CircularProgressbarWithChildren value={35}>
          <div style={{ fontSize: 25, marginTop: -5 }}>
            <strong>{numDifferentiation(status.Recovered)}</strong>
          </div>
        </CircularProgressbarWithChildren>
        <h4>Recovered </h4>
      </div>
      <div className="circlestaus" style={circleStyleI}>
        <CircularProgressbarWithChildren value={35}>
          <div style={{ fontSize: 25, marginTop: -5 }}>
            <strong>
              {numDifferentiation(status.Active + status.Confirmed)}
            </strong>
          </div>
        </CircularProgressbarWithChildren>
        <h4>Total </h4>
      </div>
      <div className="circlestaus" style={circleStyleI}>
        <CircularProgressbarWithChildren value={35}>
          <div style={{ fontSize: 25, marginTop: -5 }}>
            <strong>{numDifferentiation(status.Active)}</strong>
          </div>
        </CircularProgressbarWithChildren>
        <h4>Active </h4>
      </div>
      <div className="circlestaus" style={circleStyleI}>
        <CircularProgressbarWithChildren value={35}>
          <div style={{ fontSize: 25, marginTop: -5 }}>
            <strong>{numDifferentiation(status.Deaths)}</strong>
          </div>
        </CircularProgressbarWithChildren>
        <h4>Deaths </h4>
      </div>
      <div className="circlestaus" style={circleStyleI}>
        <CircularProgressbarWithChildren value={35}>
          <div style={{ fontSize: 25, marginTop: -5 }}>
            <strong>{status.Province}</strong>
          </div>
        </CircularProgressbarWithChildren>
        <h4>Province </h4>
      </div>
      {/* <h4 className="smallsizeh1" style={{ fontSize: "10px", margin: "0" }}>
        {status.Country}
      </h4> */}
    </Row>
  );
}

export default India;
